.address__wrapper {
  padding: 26px 20px;
  display: flex;
  justify-content: space-between;
  gap: 7px;
  align-items: center;
}
.address__wrapper--form {
  background-color: #f2f2f2;
}
.address {
  position: relative;
}
.address__title {
  margin: 0;
  padding: 0;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 20px;
  color: #000000;
  text-align: left;
}
.address__description {
  line-height: 20px;
  font-style: normal;
  max-width: 140px;
}
.address__map {
  width: 100%;
}
@media (min-width: 768px) {
  .address__wrapper {
    padding: 56px 70px;
    background-color: #f2f2f2;
  }
  .address__title {
    max-width: 172px;
    font-size: 20px;
    line-height: 26px;
  }
  .address__description {
    max-width: 160px;
  }
}
@media (min-width: 1440px) {
  .address__wrapper {
    position: absolute;
    background-color: #ffffff;
    top: 95px;
    left: 110px;
    width: 565px;
    padding: 56px 80px;
    box-sizing: border-box;
  }
}
/*colors*/
html {
  height: 100%;
}
body {
  margin: 0 auto;
  max-width: 1440px;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}
img {
  display: block;
  width: 100%;
  height: auto;
}
.main-container,
.catalog,
.form-page {
  flex-grow: 1;
  overflow-x: hidden;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.field {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  width: 100%;
  padding: 11px 40px 11px 20px;
  border: none;
  outline: 1px solid #E7E7E7;
  box-sizing: border-box;
  text-transform: uppercase;
}
