/*размер заголовков */
.main-footer__wrapper {
  background-color: #f2f2f2;
  padding: 0 20px;
}
.main-footer__logo-footer {
  display: flex;
  justify-content: center;
  padding-top: 39px;
  padding-bottom: 32px;
  border-bottom: 1px solid #d9d9d9;
}
.main-footer__logo-footer-image {
  width: 128px;
}
.main-footer__icon-text {
  fill: #111111;
}
.main-footer__icon-text:hover {
  opacity: 0.8;
}
.main-footer__icon-text:active {
  opacity: 0.6;
}
.main-footer__sotial-media {
  padding-top: 23px;
  padding-bottom: 18px;
  border-bottom: 1px solid #d9d9d9;
}
.main-footer__sotial-media__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  gap: 24px;
  justify-content: center;
}
.main-footer__htmlacademy__title {
  margin: 0;
  padding: 0;
  color: #444444;
  font-size: 16px;
  line-height: 19px;
}
.main-footer__htmlacademy {
  padding-top: 22px;
  padding-bottom: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 23px;
}
.main-footer__htmlacademy-link {
  text-decoration: none;
}
.main-footer__sotial-media__icon,
.main-footer__htmlacademy-icon {
  fill: #666666;
}
.main-footer__sotial-media__icon:hover,
.main-footer__htmlacademy:hover .main-footer__htmlacademy-icon {
  fill: #68b738;
}
.main-footer__sotial-media__icon:active,
.main-footer__htmlacademy:active {
  fill: #68b738;
  opacity: 0.3;
}
