.navigation {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  background-color: #ffffff;
  position: absolute;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  min-height: 65px;
  left: 0;
  top: 65px;
  width: 100%;
  text-align: center;
  z-index: 10;
}
.navigation__item {
  padding: 22px 0;
  position: relative;
  border-bottom: 1px solid #E6E6E6;
}
.navigation__item:first-child {
  border-top: 1px solid #E6E6E6;
}
.navigation__wrapper a {
  color: #000000;
  text-decoration: none;
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.navigation__toggle {
  width: 24px;
  height: 24px;
  border: none;
  background-color: transparent;
  position: relative;
  padding: 0;
}
.navigation--nojs .navigation__toggle {
  display: none;
}
.navigation--nojs .navigation__wrapper {
  position: static;
  min-height: 0;
}
.navigation--nojs .navigation__item {
  display: block;
}
@media (max-width: 767px) {
  .navigation--closed .navigation__list {
    display: none;
  }
}
@media (min-width: 768px) {
  .navigation__wrapper {
    display: flex;
    position: static;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    background-color: transparent;
    z-index: 10;
  }
  .navigation {
    position: static;
    display: flex;
    justify-content: flex-end;
  }
  .navigation__list {
    display: flex;
    gap: 35px;
  }
  .navigation__item {
    border-bottom: none;
    position: relative;
  }
  .navigation__item:hover {
    opacity: 0.6;
  }
  .navigation__item--current:active::after {
    content: "";
    width: 100%;
    height: 2px;
    background-color: #68b738;
    position: absolute;
    bottom: 16px;
    left: 0;
  }
  .navigation__item:first-child {
    border-top: none;
  }
  .navigation__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .navigation__item--index .navigation__item--current:active::after {
    background-color: #ffffff;
  }
  .navigation__item a {
    text-decoration: none;
  }
  .navigation__item--index-white a {
    color: #ffffff;
  }
  .navigation {
    background-color: transparent;
  }
}
/*стили для index.html*/
.advantages {
  padding: 0 20px;
  margin-bottom: 49px;
}
.advantages__title {
  margin: 0;
  margin-bottom: 40px;
}
.advantages__item-description {
  margin: 0;
  padding: 0;
}
.advantages__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  gap: 20px;
}
.advantages__item--leaf,
.advantages__item--powder,
.advantages__item--food,
.advantages__item--time {
  padding-left: 80px;
  position: relative;
  min-height: 60px;
}
.advantages__item--leaf--back_element {
  position: relative;
}
.advantages__item--leaf::before {
  content: "";
  display: block;
  position: absolute;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-image: url("../img/advantages_icon/leaf.svg");
}
.advantages__item--powder::before {
  content: "";
  display: block;
  position: absolute;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-image: url("../img/advantages_icon/powder.svg");
}
.advantages__item--food::before {
  content: "";
  display: block;
  position: absolute;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-image: url("../img/advantages_icon/food.svg");
}
.advantages__item--time::before {
  content: "";
  display: block;
  position: absolute;
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-size: cover;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  background-image: url("../img/advantages_icon/time.svg");
}
@media (min-width: 768px) {
  .advantages {
    padding: 0 30px;
    border-bottom: 1px solid #E6E6E6;
    padding-bottom: 47px;
    margin-bottom: 43px;
  }
  .advantages__list {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }
  .advantages__item {
    display: grid;
    grid-template-columns: 115px 115px;
    grid-template-rows: 140px 140px;
    align-items: start;
  }
  .advantages__wrapper .advantages__title {
    font-size: 60px;
    line-height: 60px;
  }
  .advantages__item-description {
    line-height: 24px;
    grid-row: 2 / -1;
    grid-column: 1 / -1;
  }
  .advantages__item--leaf,
  .advantages__item--powder,
  .advantages__item--food,
  .advantages__item--time {
    padding: 0;
  }
  .advantages__item--leaf::before,
  .advantages__item--powder::before,
  .advantages__item--food::before,
  .advantages__item--time::before {
    width: 80px;
    height: 80px;
    grid-column: 1 / 1;
    grid-row: 1 / 1;
  }
  .advantages__item--leaf::after {
    content: "1";
    position: absolute;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 280px;
    color: #f2f2f2;
    right: 0;
    grid-column: 2 / 2;
    grid-row: 2 / -1;
    z-index: -1;
  }
  .advantages__item--powder::after {
    content: "2";
    position: absolute;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 280px;
    color: #f2f2f2;
    right: 0;
    grid-column: 2 / 2;
    grid-row: 2 / -1;
    z-index: -1;
  }
  .advantages__item--food::after {
    content: "3";
    position: absolute;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 280px;
    color: #f2f2f2;
    right: 0;
    grid-column: 2 / 2;
    grid-row: 2 / -1;
    z-index: -1;
  }
  .advantages__item--time::after {
    content: "4";
    position: absolute;
    font-family: "Oswald", sans-serif;
    font-weight: 400;
    font-size: 280px;
    color: #f2f2f2;
    right: 0;
    grid-column: 2 / 2;
    grid-row: 2 / -1;
    z-index: -1;
  }
}
