.program-weight {
  padding: 0 20px;
  margin-bottom: 20px;
}
.program-weight__wrapper {
  display: grid;
  gap: 20px;
}
.program-weight__card {
  padding: 30px 33px 20px 20px;
  background-color: #f2f2f2;
  position: relative;
}
.program-weight__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  font-size: 24px;
  line-height: 37px;
  color: #000000;
}
.program-weight__title--thin,
.program-weight__title--muscular {
  padding-left: 70px;
  position: relative;
  margin-bottom: 30px;
}
.program-weight__title--thin::before {
  background-image: url("../img/weight_loss_icon.svg");
  left: 7px;
  z-index: 1;
  content: "";
  position: absolute;
  display: block;
  width: 50px;
  height: 53px;
  top: 0;
  bottom: 0;
  margin: auto;
  background-repeat: no-repeat;
